body {
  margin: 0px;
  padding: 0px;
  width: 100%;
  overflow-x: hidden;
  --header-backgroundColor: white;
  --ul-backgroundColor: black;
  overflow-x: hidden;
  --bar--backgroundColor: black;
}
.App {
  background-color: rgb(246 249 252);
  --header-backgroundColor: rgb(243, 244, 245);
  --ul-backgroundColor: black;
  --bar--backgroundColor: black;
}
.dark {
  background-color: #333;
  color: white;
  --header-backgroundColor: rgb(54, 54, 54);
  --ul-backgroundColor: white;
  --bar--backgroundColor: white;
}
header {
  height: 60px;
  width: 100%;
  position: relative;
  border: 0px black solid;
  background-color: var(--header-backgroundColor);
  box-shadow: 0px 0px 3px black;
  display: flex;
  align-items: center;
}
h1 {
  margin-left: 20px;
  font-family: "Hubballi", cursive;
  font-family: "Noto Sans", sans-serif;
  font-family: "Nunito Sans", sans-serif;
  font-family: "Open Sans", sans-serif;
  font-family: "Poppins", sans-serif;
  font-family: "Roboto", sans-serif;
  font-family: "Ubuntu", sans-serif;
}
.profile {
  background-color: #333;
  border-radius: 50%;
  height: 200px;
  width: 200px;
  object-fit: cover;
}
.menu {
  display: none;
  position: relative;
  margin-left: 20px;
}
.bar {
  height: 5px;
  width: 35px;
  border-radius: 2.5px;
  background-color: var(--bar--backgroundColor);
}
.bar:nth-child(2) {
  margin-top: 4px;
}
.bar:nth-child(3) {
  margin-top: 4px;
}
.main-container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* margin-top: 100px; */
  overflow: hidden;
}
.sub-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px;
  max-width: 400px;
  flex-wrap: wrap;
}
.hello {
  /* font-family: "Nunito Sans", sans-serif;
  font-family: "Poppins", sans-serif;
  font-family: "Roboto", sans-serif; */
  font-family: "Hubballi", cursive;
  font-family: "Nunito Sans", sans-serif;
  font-family: "Poppins", sans-serif;
  font-family: "Roboto", sans-serif;
  font-family: "Ubuntu", sans-serif;
  font-size: 38px;
  font-weight: 600;
}
.intro {
  margin: 10px;
  font-size: 22px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 300;
  flex-wrap: wrap;
  /* font-family: "Hubballi", cursive;
  font-family: "Nunito Sans", sans-serif;
  font-family: "Poppins", sans-serif;
  font-family: "Roboto", sans-serif; */
  /* font-family: "Open Sans", sans-serif; */
  font-family: sans-serif;

  font-weight: 500;
}

.icons {
  display: flex;
  flex-direction: row;
  margin-top: 10px;
}

.audio:not(.profile) {
  height: 30px;
  cursor: pointer;
  width: 30px;
  margin-left: 10px;
}

.audio {
  margin: 10px;
}

.p {
  text-align: center;
  font-size: 20px;
  font-weight: 200;
  font-family: "Open Sans", sans-serif;
}
.secondary-icons {
  margin-top: 20px;
}
.secondary-icons img {
  height: 40px;
  width: 40px;
  border-radius: 50%;
  object-fit: center;
  margin-left: 20px;
  cursor: pointer;
}
@media (max-width: 700px) {
  header {
    justify-content: center;
  }
  ul {
    display: none;
  }
  .menu {
    position: absolute;
    left: 20px;
    display: flex;
    flex-direction: column;
    cursor: pointer;
  }
  h1 {
    margin-left: 0px;
    font-size: 35px;
  }
}
@media (max-width: 400px) {
  .hello {
    font-size: 30px;
  }
  .intro {
    font-size: 18px;
  }
  .menu {
    position: absolute;
    left: 0px;
  }
}
@media (max-width: 320px) {
  h1 {
    font-size: 30px;
  }
  .menu {
    position: absolute;
    left: -5px;
  }
}
.theme {
  position: absolute;
  right: -25px;
  width: 50px;
  height: 50px;
  transition: all 0.5s ease-in-out;
  cursor: pointer;
}

ul {
  position: absolute;
  right: 5%;
}

li {
  position: relative;
  float: right;
  list-style: none;
  margin-left: 20px;
  cursor: pointer;
  font-size: 19px;
  font-weight: 500;
  font-family: "Hubballi", cursive;
  font-family: "Noto Sans", sans-serif;
  font-family: "Nunito Sans", sans-serif;
  font-family: "Open Sans", sans-serif;
  font-family: "Poppins", sans-serif;
  font-family: "Roboto", sans-serif;
  font-family: "Ubuntu", sans-serif;
}

li::after {
  position: absolute;
  content: "";
  bottom: 0%;
  left: 0%;
  right: 0%;
  height: 1px;
  width: 100%;
  background-color: var(--ul-backgroundColor);
  transform: scale(0);
  transition: all 0.8s ease-in-out;
}
li:hover::after {
  transform: scale(1);
}
